<style lang="scss" scoped>
    $baseFontSize: 75;
    .personrule {
        width: 100%;
        height: 325rem/$baseFontSize;
        background: url("../../assets/image/gerenzhongxin_beijing.png") no-repeat center center;
        background-size: 100% 100%;
        /*background-size: cover;*/
        color: #fff;
        position: relative;
        .ruleContent {
            position: absolute;
            top: 0;
            background: #fff;
            margin: 122rem/$baseFontSize 40rem/$baseFontSize;
            border-radius: 10px;
            padding: 35rem/$baseFontSize 28rem/$baseFontSize;
            box-shadow: 0px 10px 31px 6px rgba(209, 209, 209, 0.25);
            .title {
                color: #3d3d3d;
                font-size: 30rem/$baseFontSize;
                position: relative;
                line-height: 60rem/$baseFontSize;
                font-weight: 600;
            }
            .title:before {
                position: absolute;
                content: '';
                display: inline-block;
                height: 22rem/$baseFontSize;
                width: 16rem/$baseFontSize;
                border-radius: 0 3px 3px 0;
                background: #0abf9b;
                left: -28rem/$baseFontSize;
                top: 19rem/$baseFontSize;
            }
            .content {
                color: #3d3d3d;
                font-size: 24rem/$baseFontSize;
                line-height: 60rem/$baseFontSize;
                .tableBox {
                    padding: 15rem/$baseFontSize 0;
                    .ruleTable {
                        width: 100%;
                        border-collapse: separate;
                        border-spacing: 0;
                        border: 2px #000;
                        text-align: center;
                        line-height: 60rem/$baseFontSize;
                        font-size: 24rem/$baseFontSize;
                    }
                    table tr th, table tr td {
                        border-left: 1px solid #d2d2d2;
                    }
                    table tr th:last-child, table tr td:last-child { /*table*/
                        border-right: 1px solid #d2d2d2;
                    }
                    table tr td { /*table*/
                        border-bottom: 1px solid #d2d2d2;
                    }
                    table tr th {
                        background: #dcdcdc;
                        text-align: center;
                    }
                    table tr:first-child th:first-child {
                        border-top-left-radius: 10px;
                    }

                    table tr:first-child th:last-child {
                        border-top-right-radius: 10px;
                    }
                    table tr:last-child td:first-child {
                        border-bottom-left-radius: 10px;
                    }

                    table tr:last-child td:last-child {
                        border-bottom-right-radius: 10px;
                    }
                }
            }
        }
    }
</style>
<template>
    <div>
        <div class="personrule">
            <div class="ruleContent">
                <div class="title">1.</div>
                <div class="content">
                    V0-V5
                </div>
                <div class="title">2.</div>
                <div class="content">
                    
                </div>
                <div class="title">3.</div>
                <div class="content">
                    <div class="tableBox">
                        <table class="ruleTable" cellspacing="0" cellpadding="0">
                            <tr>
                                <th></th>
                                <th></th>
                                <th>XX</th>
                            </tr>
                            <tr>
                                <td>V0</td>
                                <td></td>
                                <td>0-499</td>
                            </tr>
                            <tr>
                                <td>V0</td>
                                <td></td>
                                <td>0-499</td>
                            </tr>
                            <tr>
                                <td>V0</td>
                                <td></td>
                                <td>0-499</td>
                            </tr>
                        </table>
                    </div>

                </div>
                <div class="title">4.</div>
                <div class="content">  
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "tixianrule",
        data() {
            return {}
        },
        created() {
            document.getElementsByTagName('title')[0].innerHTML = ''
        },
    }
</script>





