mg {\n        height: 20px;\n        width: 19px;\n        position: absolute;\n        left: 20px;\n        padding: 10px 0;\n    }\n    .title {\n        display: inline-block;\n        vertical-align: middle;\n    }\n}\n\n.iconInfo {\n    width: 100%;\n    height: 325rem/$baseFontSize;\n    background: url(\"../../../assets/image/gerenzhongxin_beijing.png\") no-repeat center center;\n    background-size: 100% 100%;\n    /*background-size: cover;*/\n    color: #fff;\n    .speadInfoTop {\n        width: 100%;\n        padding-top: 80rem/$baseFontSize;\n        padding-bottom: 132rem/$baseFontSize;\n        .personName {\n            height: 60px;\n            line-height: 60px;\n            .name {\n                font-size: 48rem/$baseFontSize;\n                font-weight: 700;\n            }\n            .nameClass {\n                line-height: 24px;\n                font-size: 28rem/$baseFontSize;\n            }\n        }\n        .personIcon {\n            height: 56px;\n            width: 56px;\n            border-radius: 50%;\n            border: 2px solid #fff;\n        }\n        .headBox {\n            position: relative;\n            .personIcon {\n                height: 56px;\n                width: 56px;\n                border-radius: 50%;\n                border: 2px solid #fff;\n            }\n            .vip {\n                position: absolute;\n                height: 35px;\n                width: 35px;\n                top: -8px;\n                left: -8px;\n                z-index: 100;\n            }\n        }\n    }\n    .iconInfoTop {\n        padding-top: 45px;\n        .personName {\n            height: 60px;\n            .name {\n                line-height: 35px;\n                font-size: 48rem/$baseFontSize;\n                font-weight: 700;\n            }\n        }\n\n    }\n}\n\n.iconInfoBottom {\n    .lineWeight {\n        line-height: 66rem/$baseFontSize;\n        font-weight: 900;\n        .backline {\n            display: inline-block;\n            vertical-align: middle;\n            height: 15px;\n            width: 3px;\n            background: #dcdddd;\n            border-radius: 3px;\n        }\n    }\n    .nameClass {\n        font-size: 32rem/$baseFontSize;\n        color: #3d3d3d;\n        font-weight: 800;\n        line-height: 66rem/$baseFontSize;\n    }\n    .money {\n        color: #3d3d3d;\n        font-size: 32rem/$baseFontSize;\n        font-weight: 600;\n        line-height: 32rem/$baseFontSize;\n        .personText {\n            font-size: 24rem/$baseFontSize;\n            font-weight: 600;\n            display: inline-block;\n            line-height: 24rem/$baseFontSize;\n        }\n    }\n    .moneyExplain {\n        font-size: 12px;\n        color: #929292;\n    }\n}\n\n.wealth {\n    padding: 6px 20px 4px;\n    margin-top: -23px;\n    /*border-top: 10px solid #fafafa;*/\n    background: #fff;\n    .wealthTop {\n        color: #3d3d3d;\n        line-height: 40px;\n        .wealthTopLeft {\n            display: inline-block;\n            vertical-align: middle;\n            span {\n                display: inline-block;\n                vertical-align: middle;\n            }\n            .myWealth {\n                font-size: 30rem/$baseFontSize;\n                font-weight: 700;\n            }\n            .applayWealth {\n                font-size: 24rem/$baseFontSize;\n            }\n        }\n        .wealthTopRight {\n            display: inline-block;\n            vertical-align: middle;\n            span {\n                display: inline-block;\n                vertical-align: middle;\n            }\n            .tixianIcon {\n                padding-left: 5px;\n                height: 20px;\n                width: 20px;\n                vertical-align: middle;\n            }\n        }\n    }\n    .wealthBottom {\n        /*margin-top: 20px;*/\n        padding: 20px;\n        /*border: 1px solid #fff;*/\n        /*border-radius: 10px;*/\n        /*box-shadow:0px 0px 15px 6px rgba(209,209,209,0.25);*/\n        text-align: center;\n        .weathmoney {\n            font-size: 32rem/$baseFontSize;\n            color: #ff8f34;\n            font-weight: 700;\n        }\n        .moneyExplain {\n            font-size: 12px;\n            padding-top: 6px;\n            color: #929292;\n        }\n    }\n}\n\n.infoList {\n    background: #fff;\n    margin: 20rem/$baseFontSize 0;\n    padding: 15px 20px;\n    text-align: center;\n    color: #3d3d3d;\n    a {\n        color: #3d3d3d;\n    }\n    .xianbaoImg {\n        height: 45px;\n        width: 45px;\n        margin-bottom: 6px;\n    }\n}\n\n.share {\n    width: 100%;\n    height: 122rem/$baseFontSize;\n    background: url('../../../assets/image/guanggao_beijing.png') no-repeat center center;\n    background-size: 100% 100%;\n    margin-top: 20rem/$baseFontSize;\n    margin-bottom: 40rem/$baseFontSize;\n    .shareBox {\n        padding: 22rem/$baseFontSize 20px;\n        line-height: 42rem/$baseFontSize;\n        .shareTitle {\n            font-size: 30rem/$baseFontSize;\n            color: #fff;\n            font-weight: 600;\n        }\n        .shareText {\n            color: #fff;\n            font-size: 24rem/$baseFontSize;\n        }\n    }\n    /*.buttonBox{*/\n    /*<!--line-height: 164rem/$baseFontSize;-->*/\n    .shareButton {\n        display: inline-block;\n        vertical-align: middle;\n        color: #e75858;\n        background: #fff;\n        height: 60rem/$baseFontSize;\n        line-height: 60rem/$baseFontSize;\n        width: 147rem/$baseFontSize;\n        margin: 31rem/$baseFontSize 20px;\n        text-align: center;\n        font-size: 24rem/$baseFontSize;\n        border-radius: 5px;\n    }\n    /*}*/\n\n    a:active {\n        background: red;\n    }\n}\n\n.order {\n    padding: 0 20px;\n    background: #fff;\n    .orderBoxOne {\n        background: #fff;\n        position: relative;\n        top: -68rem/$baseFontSize;\n        padding: 26px;\n        border-radius: 10px;\n        box-shadow: 0px 0px 15px 6px rgba(209, 209, 209, 0.25);\n        text-align: center;\n        .orderTop {\n            // padding: 0 0 20px;\n            // border-bottom: 1px solid #eeeeee;\n            // display: flex;\n            .orderbefore {\n                position: relative;\n                width: 50%;\n            }\n            .lineWeight {\n                line-height: 66rem/$baseFontSize;\n                font-weight: 900;\n                .backline {\n                    display: inline-block;\n                    vertical-align: middle;\n                    height: 15px;\n                    width: 3px;\n                    background: #dcdddd;\n                    border-radius: 3px;\n                }\n            }\n            // .orderbefore:after {\n            //     content: '';\n            //     position: absolute;\n            //     background: #eeeeee;\n            //     display: inline-block;\n            //     height: 100rem/$baseFontSize;\n            //     width: 1px;\n            //     margin-top: -30rem/$baseFontSize;\n            //     bottom: -20px;\n            //     right: 0;\n            // }\n            .orderafter {\n                width: 50%;\n            }\n        }\n        .orderBottom {\n            padding: 20px 0px 0px;\n            display: flex;\n            .orderbeforetwo {\n                position: relative;\n                width: 50%;\n            }\n            .orderbeforetwo:after {\n                content: '';\n                position: absolute;\n                background: #eeeeee;\n                display: inline-block;\n                height: 100rem/$baseFontSize;\n                width: 1px;\n                margin-top: -30rem/$baseFontSize;\n                top: -5px;\n                right: 0;\n            }\n            .orderafter {\n                width: 50%;\n            }\n        }\n        .weathmoney {\n            font-size: 32rem/$baseFontSize;\n            color: #ff8f34;\n            font-weight: 700;\n            line-height: 32rem/$baseFontSize;\n        }\n        .moneyExplain {\n            font-size: 12px;\n            padding-top: 6px;\n            color: #636363;\n        }\n    }\n    .orderBox {\n        background: #fff;\n        position: relative;\n        top: -68rem/$baseFontSize;\n        padding: 26px;\n        border-radius: 10px;\n        box-shadow: 0px 0px 15px 6px rgba(209, 209, 209, 0.25);\n        text-align: center;\n        .vip2 {\n            position: absolute;\n            height: 40px;\n            width: 40px;\n            top: 0;\n            left: 0;\n        }\n        .orderTop {\n            padding: 0 25px 20px;\n            border-bottom: 1px solid #eeeeee;\n        }\n        .orderBottom {\n            padding: 20px 25px 0px;\n        }\n        .weathmoney {\n            font-size: 32rem/$baseFontSize;\n            color: #ff8f34;\n            font-weight: 700;\n            line-height: 32rem/$baseFontSize;\n        }\n        .moneyExplain {\n            font-size: 12px;\n            padding-top: 6px;\n            color: #636363;\n        }\n    }\n}\n\n.orderManage {\n    margin-top: 10px;\n    background: #fff;\n    padding: 40rem/$baseFontSize;\n    height: 45px;\n    line-height: 45px;\n    color: #3d3d3d;\n    font-size: 30rem/$baseFontSize;\n    .orderIcon {\n        height: 45px;\n        width: 45px;\n        vertical-align: middle;\n        margin-right: 10px;\n    }\n    .goOrder {\n        height: 20px;\n        width: 20px;\n        vertical-align: middle;\n    }\n}\n\n.helpcenter {\n    margin-top: 20rem/$baseFontSize;\n    background: #fff;\n    padding: 25rem/$baseFontSize 40rem/$baseFontSize;\n    height: 50rem/$baseFontSize;\n    line-height: 40rem/$baseFontSize;\n    color: #3d3d3d;\n    font-size: 28rem/$baseFontSize;\n    .helpIcon {\n        height: 40rem/$baseFontSize;\n        width: 40rem/$baseFontSize;\n        display: inline-block;\n        vertical-align: middle;\n        margin-right: 10px;\n    }\n    span {\n        display: inline-block;\n        vertical-align: middle;\n    }\n    .gohelpcenter {\n        width: 36rem/$baseFontSize;\n        height: 36rem/$baseFontSize;\n        vertical-align: middle;\n    }\n}\n\n.bottom {\n    background: #fff;\n    margin-top: 60px;\n    box-shadow: 0px 0px 15px 6px rgba(209, 209, 209, 0.25);\n    text-align: center;\n    position: fixed;\n    bottom: 0;\n    width: 100%;\n    height: 100rem/$baseFontSize;\n    z-index: 100;\n    display: flex;\n    justify-content: center;\n    .iconBoxone {\n        margin-right: 80rem/$baseFontSize;\n    }\n    .iconBoxtwo {\n        margin-left: 80rem/$baseFontSize;\n    }\n    .iconBox {\n        margin-top: -25rem/$baseFontSize;\n        color: #d4d4d4;\n        font-size: 22rem/$baseFontSize;\n        .select {\n            color: #0abf9b;\n            margin-top: -14px;\n        }\n        .noselect {\n            margin-top: -14px;\n        }\n        .icon {\n            border-radius: 50%;\n            height: 100rem/$baseFontSize;\n            width: 100rem/$baseFontSize;\n            /*<!--box-shadow:1px -6px 13px -3px rgba(106,106,106,0.25);-->*/\n            box-shadow: 2px -27px 20px -6px rgba(209, 209, 209, 0.25);\n        }\n    }\n}\n"],"sourceRoot":""}]);

