<style rel="stylesheet/scss" lang="scss" scoped>
    $bgcolor: #d51e12;
    $fontColor: #fff;
    $bgtc: #ececec;
    $baseFontSize: 75;
    .treasureTop {
        padding: 25px 15px 10px;
        .applayMouth {
            font-size: 28rem / $baseFontSize;
            font-weight: 600;
            color: #3d3d3d;
        }
        .applayTotal {
            color: #ff8f34;
            font-size: 24rem / $baseFontSize;
            font-weight: 600;
        }
        .finish {
            color: #0abf9b;
        }
        .fail {
            color: #ed5050;
        }
        .black {
            color: #929292;
        }
    }

    .treasureBox {
        margin: 15px;
        border-radius: 10px;
        background: #fff;
        box-shadow: 0px 0px 15px 6px rgba(209, 209, 209, 0.25);
        .product {
            padding: 15px 15px 0;
            font-size: 28rem / $baseFontSize;
            color: #929292;
        }
        .name {
            font-size: 32rem / $baseFontSize;
            color: #0abf9b;
            padding: 15px 15px 0;
        }
        .linebox {
            border-top: 1px solid #f0f0f0;
            padding-bottom: 15px;
        }
        .lineboxone {
            padding-bottom: 15px;
        }
    }

    .fixedBox {
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 110rem / $baseFontSize;
        color: #fff;
        line-height: 110rem / $baseFontSize;
        text-align: center;
        font-size: 28rem / $baseFontSize;
        background: -webkit-linear-gradient(
                        left,
                        #0abf9b,
                        #14baff
        ); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(
                        right,
                        #0abf9b,
                        #14baff
        ); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(
                        right,
                        #0abf9b,
                        #14baff
        ); /* Firefox 3.6 - 15 */
        background: linear-gradient(to right, #0abf9b, #14baff);
        /*  */
    }

    .orderDetail {
        min-height: 100vh;
        padding-bottom: 1rem / $baseFontSize;
    }

    .tableBox {
        padding: 15rem / $baseFontSize 15px;
        .ruleTable {
            width: 100%;
            border-collapse: separate;
            border-spacing: 0;
            border: 2px #000;
            text-align: center;
            line-height: 60rem / $baseFontSize;
            font-size: 24rem / $baseFontSize;
        }
        table tr th,
        table tr td {
            border-left: 1px solid #d2d2d2;
        }
        table tr th:last-child,
        table tr td:last-child {
            /* table */
            border-right: 1px solid #d2d2d2;
        }
        table tr td {
            /* table */
            border-bottom: 1px solid #d2d2d2;
        }
        table tr th {
            background: #dcdcdc;
            text-align: center;
        }
        table tr:first-child th:first-child {
            border-top-left-radius: 10px;
        }
        table tr:first-child th:last-child {
            border-top-right-radius: 10px;
        }
        table tr:last-child td:first-child {
            border-bottom-left-radius: 10px;
        }
        table tr:last-child td:last-child {
            border-bottom-right-radius: 10px;
        }
    }
</style>
<template>
    <div class='orderDetail'>
        <div v-if='hasdata' style="margin-bottom: 88px;position: relative;">
            <div class="flexBetween treasureTop">
                <span class="applayMouth">:{{detailData.orderNumber}}</span>
                <span class="applayTotal " v-if="detailData.status == 0"></span>
                <span class="applayTotal" v-if="detailData.status == 1"></span>
                <span class="applayTotal" v-if="detailData.status == 2"></span>
                <span class="applayTotal fail" v-if="detailData.status == 3"></span>
                <span class="applayTotal" v-if="detailData.status == 4"></span>
                <span class="applayTotal black" v-if="detailData.status == 5"></span>
                <span class="applayTotal finish" v-if="detailData.status == 6"></span>
                <span class="applayTotal black" v-if="detailData.status == 7"></span>
            </div>
            <div class="treasureBox" v-if='riskList.length != 0'>
                <div class="name"></div>
                <div class='lineboxone' v-for="(item,index) in riskList" v-if='index == 0' :key='(item,index)'>
                    <p class="product">{{item.riskName}}</p>
                    <p class="product">{{item.insuranceCompany}}</p>
                    <p class="product">{{item.insuredAmount}}</p>
                    <p class="product">{{item.insuYear}}</p>
                    <p class="product">{{item.premium.toLocaleString()}}</p>
                    <p class="product">{{item.effectiveDate}}</p>
                    <p class="product">{{item.endDate}}</p>
                </div>
                <div class='linebox' v-for="(item,index) in riskList" v-if='index == 1' :key='(item,index)'>
                    <p class="product">{{item.riskName}}</p>
                    <p class="product">{{item.insuranceCompany}}</p>
                    <p class="product">{{item.insuredAmount}}</p>
                    <p class="product">{{item.insuYear}}</p>
                    <p class="product">{{item.premium.toLocaleString()}}</p>
                    <p class="product">{{item.effectiveDate}}</p>
                    <p class="product">{{item.endDate}}</p>
                </div>
                <div class='linebox' v-for="(item,index) in riskList" v-if='index == 2' :key='(item,index)'>
                    <p class="product">{{item.riskName}}</p>
                    <p class="product">{{item.insuranceCompany}}</p>
                    <p class="product">{{item.insuredAmount}}</p>
                    <p class="product">{{item.insuYear}}</p>
                    <p class="product">{{item.premium.toLocaleString()}}</p>
                    <p class="product">{{item.effectiveDate}}</p>
                    <p class="product">{{item.endDate}}</p>
                </div>
            </div>

            <div class="treasureBox">
                <div class="name"></div>
                <p class="product">{{applicant.name}}</p>
                <p class="product" v-if="applicant.idType == 1"></p>
                <p class="product" v-if="applicant.idType == 2"></p>
                <p class="product">{{applicant.idNumber}}</p>
                <p class="product" v-if='applicant.idValidType==1'></p>
                <p class="product" v-if='applicant.idValidType==2'></p>
                <p class="product">{{applicant.phone}}</p>
                <p class="product">{{applicant.email}}</p>
                <p class="product">{{applicant.email}}</p>
                <p class="product lineboxone">{{applicant.address}}</p>
            </div>
            <div class="treasureBox">
                <div class="name"></div>
                <div class='' v-for="(item,index) in insuredList" v-if='index == 0' :key='(item,index)'>
                    <p class="product">1{{item.name}}</p>
                    <p class="product">{{getRelationStatus(item.relation)}}</p>
                    <p class="product" v-if="item.idType == 1"> </p>
                    <p class="product" v-if="item.idType == 2"> </p>
                    <p class="product lineboxone">{{item.idNumber}}</p>
                    <p class="product" v-if='item.idValidType==1'></p>
                    <p class="product" v-if='item.idValidType==2'></p>
                    <!-- <p class="product">{{item.idNumber}}</p>
                    <p class="product">{{item.idNumber}}</p>
                    <p class="product lineboxone">{{applicant.address}}</p> -->
                </div>
                <div class='linebox' v-for="(item,index) in insuredList" :key='(item,index)' v-else>
                    <p class="product">{{index+1}}{{item.name}}</p>
                    <p class="product">{{getRelationStatus(item.relation)}}</p>
                    <p class="product" v-if="item.idType == 1"> </p>
                    <p class="product" v-if="item.idType == 2"> </p>
                    <p class="product lineboxone">{{item.idNumber}}</p>
                    <p class="product" v-if='item.idValidType==1'></p>
                    <p class="product" v-if='item.idValidType==2'></p>
                </div>
            </div>
            <div class="treasureBox" v-if='beneficiaryType == 1'>
                <div class="name"></div>
                <div class='' v-for="(item,index) in beneficiaryList" v-if='index == 0' :key='(item,index)'>
                    <p class="product">{{item.name}}</p>
                    <p class="product">{{getRelationStatus(item.type)}}</p>
                    <p class="product" v-if="item.idType == 1"> </p>
                    <p class="product" v-if="item.idType == 2"> </p>
                    <p class="product lineboxone">{{item.idNumber}}</p>
                    <p class="product">{{item.ratio}}</p>
                    <p class="product">{{item.idNumber}}</p>

                </div>
                <div class='linebox' v-for="(item,index) in beneficiaryList" :key='(item,index)' v-else>
                    <p class="product">{{item.name}}</p>
                    <p class="product">{{getRelationStatus(item.type)}}</p>
                    <p class="product" v-if="item.idType == 1"> </p>
                    <p class="product" v-if="item.idType == 2"> </p>
                    <p class="product lineboxone">{{item.idNumber}}</p>
                    <p class="product">{{item.ratio}}</p>
                    <p class="product">{{item.idNumber}}</p>
                </div>
            </div>
            <div class="treasureBox" v-if='beneficiaryType == 0'>
                <div class="name"></div>
                <div class='lineboxone'>
                    <p class="product"></p>
                </div>
            </div>
        </div>
        <div v-else class="nodata loginSucess">
            <img class='sucessLogo' src="../../../assets/image/noIcon.png">
            <div class="sucessTextBox">
                <p class="sucessText sucessTextone"></p>
                <p class="sucessText sucessTexttwo">,</p>
            </div>
            <div class="goGuanzhu" @click="toShangcheng"></div>
        </div>
    </div>
</template>
<script>
    import {Loadmore, Toast} from "mint-ui";
    import Util from "../../../common/util";
    import Service from "../../../common/service";
    import Global from "../../../common/global";
    import Filter from '../../../common/filter'

    export default {
        name: "enjoytreasure",
        data() {
            return {
                hasdata: true,
                id: "",
                concatData: [], //
                Data: [], //
                applicant: {},
                beneficiaryList: [],
                insuredList: [],
                detailData: {},
                riskList: [],
                beneficiaryType: "",
                productId: ''
            };
        },
        created() {
            document.getElementsByTagName("title")[0].innerHTML = "";
            this.id = this.$route.query.id;
            setTimeout(this.getDetail(), 13);
        },
        methods: {
            getRelationStatus(state) {
                return Filter.getRelationStatus(state)
            },
            getDetail() {
                Service.order()
                    .getOrderpolicy({}, this.id)
                    .then(
                        response => {
                            this.applicant = response.data.applicant;
                            this.beneficiaryList = response.data.beneficiaryList;
                            this.insuredList = response.data.insuredList;
                            this.detailData = response.data;
                            this.riskList = response.data.riskList;
                            this.beneficiaryType = response.data.beneficiaryType;
                            this.productId = response.data.productId;
                        },
                        err => {
                        }
                    );
            }
        }
    };
</script>





