<template>
    <div class="container">
        <mt-header fixed title="">
        </mt-header>
        <div class="policyconfirm-doctext">
            1
            65BCT
            2   /////?
            3  
            :160mmHg100mmHgHIV
            ?
            4   100?
            5   
        </div>
        <div class="footer flexBetween">
            <div class="cancel-btn" @click="openToast()">
                
            </div>
            <div class="sure-btn" @click="goInfoWrite()">
                
            </div>
        </div>
    </div>
</template>
<script>
    import Util from '../../common/util'
    import Service from '../../common/service'
    import {Toast} from 'mint-ui'

    export default {
        data() {
            return {
                bannerList: [],
                productList: [],
                limit: 10,
                page: 1
            }
        },
        beforeRouteEnter(to, from, next) {
            console.log(to)
            if (to.query.loginCode) {
                Service.user().postLoginCode({
                    code: to.query.loginCode
                }).then(response => {
                    console.log(response)
                    Util.localStorageUtil.set("access_token", response.data.token);
                    Util.localStorageUtil.set("userInfo", response.data);
                    let queryObj = to.query
                    delete queryObj.loginCode
                    next({
                        name: 'healthconfirm',
                        params: to.params,
                        query: queryObj,
                        replace: true
                    })
                })
            } else {
                next()
            }
        },
        watch: {},
        methods: {
            handleopen() {
                //console.log('handleopen');
            },
            handleClose() {
                //console.log('handleclose');
            },
            openToast() {
                Toast({
                    message: '',
                    duration: 4000
                });
            },
            goInfoWrite() {
                this.$router.push({
                    name: 'infowrite',
                    params: {
                        productId: this.$route.params.productId
                    },
                    query: this.$route.query
                })
            }
        },
        beforeMount() {


        },
        mounted() {

        },
        destroyed() {

        },
        created() {
            console.log(this.$route.query)
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @charset "utf-8";
    $baseFontSize: 75;
    .policyconfirm-doctext {
        padding: 40rem/$baseFontSize 40rem/$baseFontSize 100rem/$baseFontSize;
        font-size: 24rem/$baseFontSize;
        line-height: 60rem/$baseFontSize;
        color: #727070;
    }

    .footer {
        height: 100rem/$baseFontSize;
        width: 100%;
        bottom: 0;
        right: 0;
        left: 0;
        position: fixed;
        z-index: 1;
        border: solid 1px #eeeeee;
        .cancel-btn {
            width: 230rem/$baseFontSize;
            background: #FFFFFF;
            color: #00A399;
            text-align: center;
            font-size: 28rem/$baseFontSize;
            line-height: 100rem/$baseFontSize;
            &:active {
                opacity: 0.6;
            }
        }
        .sure-btn {
            width: 520rem/$baseFontSize;
            background-image: linear-gradient(90deg, #0abf9b 0%, #14baff 100%), linear-gradient(#ffffff, #ffffff);
            background-blend-mode: normal, normal;
            color: #FFFFFF;
            text-align: center;
            font-size: 28rem/$baseFontSize;
            line-height: 100rem/$baseFontSize;
            &:active {
                opacity: 0.6;
            }
        }
    }
</style>


