import Util from './util'

/**
 * JSON
 * @param  {String} json    [JSON]
 * @param  {Object} options []
 * @return {[type]}         []
 */
const getRelationStatus = (status) => {
    let statusDesc = '';
    switch (status) {
        case 1:
            statusDesc = '';
            break;
        case 2:
            statusDesc = '';
            break;
        case 3:
            statusDesc = '';
            break;
        case 4:
            statusDesc = '';
            break;
        case 5:
            statusDesc = '';
            break;
        default:
            statusDesc = '';
            break
    }
    return statusDesc
};
export default {
    getRelationStatus: getRelationStatus,
}



